<template>
  <div class="code-preview">
    <DropDown
      :options="languages"
      :selected="selectedLanguage"
      @change="onLanguageChange"
     />
  </div>
</template>

<script>
import DropDown from './DropDown';
import Languages from '../enum/Languages';

export default {
  name: 'CodePreview',

  data() {
    return {
      languages: [{
        id: Languages.CPP,
        name: 'C++'
      },
      {
        id: Languages.BASIC,
        name: 'Basic'
      },
      {
        id: Languages.INTERNAL,
        name: 'Internal Code'
      }],
      selectedLanguage: Languages.CPP
    }
  },

  methods: {
    onLanguageChange(id) {
      this.selectedLanguage = id;
    }
  },

  components: {
    DropDown
  }
}
</script>

<style lang="scss">
  .code-preview {
    width: 40%;
    padding: 20px;
  }
</style>



