<template>
  <div class="blockly-container">
    <div id="blockly" />
  </div>
</template>

<script>
import Blockly from 'node-blockly/browser';
import CustomBlocks from '../utils/blockly/custom_blocks';
import LanguageLua from '../utils/blockly/lua';
import Languagejavascript from '../utils/blockly/javascript';
import LanguageMath from '../utils/blockly/math';
import LanguageLogic from '../utils/blockly/logic';
import LanguageLoops from '../utils/blockly/loops';
import LanguageText from '../utils/blockly/text';
import CustomBlocksSyntax from '../utils/blockly/custom_blocks_syntax';
import { xml } from '../utils/blockly/toolbox';

export default {
  name: 'Header',

  mounted() {
    this.blocklyInstance = Blockly.inject("blockly", {
      toolbox: Blockly.Xml.textToDom(xml),
      media: '/static/blockly/',
      zoom: {
        controls: true,
        wheel: true,
        startScale: 1.0,
        maxScale: 3,
        minScale: 0.3,
        scaleSpeed: 1.2
      },
      trashcan: true
    });
  }
};
</script>

<style lang="scss">
  @import '../../scss/variables/colors';

  $boxShadow: 0 0 10px $colorDarkestGrey;

  .blockly-container {
    width: 60%;
    height: calc(100vh - #{$headerHeight});

    #blockly {
      height: 100%;
    }

    .blocklyToolboxDiv {
      background-color: $colorDarkBlue;
      box-shadow: $boxShadow;

      .blocklyTreeRoot {
        padding: 0;

        .blocklyTreeRow {
        	padding: 10px 20px;
          margin: 0;
        	height: auto;
        	cursor: pointer;
          transition: background-color .2s;

          &:hover {
            background-color: $colorBlue;
          }

          .blocklyTreeLabel {
            cursor: pointer;
          }
        }
      }
    }

    .blocklyFlyoutBackground {
      fill: $colorBlue;
    }

    .blocklySvg {
      background-color: $colorDarkestBlue;
      width: 101%; // hide shitty border
      height: 101%; // hide shitty border
      margin-top: -1px; // hide shitty border
      box-shadow: inset $boxShadow;
    }
  }
</style>



