<template>
  <div id="app">
    <Header />
     <main>
      <BlocklyContainer />
      <CodePreview />
     </main>
  </div>
</template>

<script>
import BlocklyContainer from './components/BlocklyContainer';
import Header from './components/Header';
import CodePreview from './components/CodePreview';

export default {
  name: 'app',

  components: {
    Header,
    BlocklyContainer,
    CodePreview
  }
};
</script>

<style lang="scss">
  #app {
    main {
      display: flex;
    }
  }
</style>



